@media only screen and (max-width: 1200px) {
  .control-label {
    font-size: 0.8em;
  }

  .display-text {
    font-size: 0.8em;
  }
}

@media only screen and (max-width: 800px) {
  .control-label {
    font-size: 0.7em;
  }

  .display-text {
    font-size: 0.7em;
  }
}

@media only screen and (max-width: 600px) {
  .outer-container {
    flex-direction: column;
  }

  .container {
    font-size: 1em;
  }

  .container#drumpad-board, .container#control-board {
    width: 66vw;
  }

  .drum-pad {
    flex: 1 auto 33%;
    min-height: 22vw;
  }

  .control-container, .display-container {
    height: 12vw;
  }

  .control-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .control-label {
    font-size: 1em;
  }

  .display-text {
    font-size: 1em;
  }
}

@media only screen and (max-width: 200px) {
  .container#control-board {
    display: none;
  }
}